<template>
  <div class="home-body">
    <div class="container" v-for="i in 6" :key="'home-container-'+i">
      <!-- 限定每列最多20个词条 -->
      <div v-if="i===1 && words">
        <span @click="objProps.setActioning(1)">添加新词条</span>
      </div>
      <div v-for="data in getarr(i)" :class="{checkingword:checkingword===data}" :key="'home-n-'+data.id" @click="objProps.setCheckingword(data)">
        <span>{{data.name}}</span>
        <div class="delete" @click.stop="h3_delete(data)">×</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeBody',
  props: ['objProps', 'words', 'checkingword'],
  methods: {
    getarr (i) {
      // 该方法计算每一栏的元素个数,n用来计算是不是第一列,以添加新词条
      var n = 1
      if (i === 1) n = 0
      return this.words && this.words.length > (i - 1) * 20 -
        n ? this.words.slice((i - 1) * 20 - n, i * 20 - 1) : null
    },
    h3_delete (data) {
      if (confirm(`确定要删除词条【${data.name}】吗`)) {
        this.objProps.axiosGetData('delete/h3', { id: data.id }, function (res) {
          if (!res.data) {
            console.log('词条删除失败了')
          }
          this.objProps.getFiles()
        }.bind(this))
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.home-body {

  .container {
    padding: 5px 0;
    width: 16.666%;
    box-sizing: border-box;
    border: 1px solid black;
    border-radius: 8px;
    margin: 5px;
    overflow: hidden;
    text-align: center;

    &>div {
      width: 100%;
      padding: 0 10px;
      height: calc((100% / 20));
      box-sizing: border-box;
      text-align: left;
      cursor: pointer;
      position: relative;

      &:hover {
        background: #eee;

        .delete {
          display: inline-block;
        }
      }

      &.checkingword {
        background: #ddd;
      }

      &>span {
        display: inline-block;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        width: 100%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      .delete {
        display: none;
        position: absolute;
        right: 8px;
        top: 2px;
        font-size: 25px;
        color: #808080;

        &:hover {
          color: #000;
        }
      }
    }

    &:first-of-type>div:first-of-type {
      text-align: center;
      display: inline-block;
      background: #00f2cc;
      color: #008888;
      width: 90%;
      border-radius: 8px;
      border: 1px solid #a0a0a0;

      &:hover {
        color: #000000;
      }
    }
  }
}
</style>



