<template>
  <div class="home">
    <loading v-if="loading"></loading>
    <home-header @loading="getLoading" :objProps="to_HomeHeader" :arrFileFolders="arrFileFolders" :spread="spread"></home-header>
    <home-body @loading="getLoading" :objProps="to_HomeBody" :words="words" :checkingword="this.checkingword"></home-body>
    <transition name="fade">
      <show-words v-if="actioning!==null" :objProps="to_ShowWords" :checkingword="checkingword" :h1="h1" :h2="h2" :actioning="this.actioning"></show-words>
    </transition>
  </div>
</template>

<script>
import HomeHeader from '@/pages/Header.vue'
import HomeBody from '@/pages/Body.vue'
import ShowWords from '@/pages/ShowWords'
import Loading from '@/components/Loading.vue'
import axiosGetData from '@/commFunction/axiosGetData'
export default {
  name: 'Home',
  components: {
    HomeHeader,
    HomeBody,
    ShowWords,
    Loading
  },
  data () {
    return {
      loading: false,
      words: null,
      checkingword: null,
      actioning: null, // 保存当前对词条行为,0对应check,1对应insert,2对应update
      h1: null,
      h2: null,
      arrFileFolders: null,
      spread: false, // 控制h1栏的展开与否
      to_HomeHeader: {
        getFiles: this.getFiles,
        setActioning: this.setActioning,
        axiosGetData: this.axiosGetData,
        getFileFolders: this.getFileFolders,
        spreadChange: this.spreadChange
      },
      to_HomeBody: {
        getFiles: this.getFiles,
        setCheckingword: this.setCheckingword,
        axiosGetData: this.axiosGetData,
        setActioning: this.setActioning
      },
      to_ShowWords: {
        getFiles: this.getFiles,
        setCheckingword: this.setCheckingword,
        axiosGetData: this.axiosGetData,
        setActioning: this.setActioning
      }
    }
  },
  mounted () {
    // 解析锚点后的内容,确认加载哪个目录的词条
    this.getFileFolders()
    this.getFiles()
  },
  methods: {
    axiosGetData,
    getLoading (data) {
      this.loading = data
    },
    getFileFolders (callback) {
      callback = callback || function () { }
      this.axiosGetData('getFileFolders', {}, function (res) {
        this.arrFileFolders = res.data
        callback()
      }.bind(this))
    },
    getFiles () {
      this.actioning = null
      this.checkingword = null
      var arr = decodeURI(location.hash).split('/')
      arr.shift()
      this.h1 = arr[0]
      this.h2 = arr[1]
      this.axiosGetData('getFiles', { h1: arr[0] || '', h2: arr[1] || '' }, function (res) {
        this.words = res.data
        if (!location.hash) this.words = null
        // 设置进入词条后若为[展开]状态,改为[收回]状态
        if (this.spread) this.spreadChange()
      }.bind(this))
    },
    setCheckingword (checkingword) {
      this.setActioning(0)
      this.checkingword = checkingword
    },
    setActioning (actioning) {
      if (this.actioning === 1) {
        if (!confirm('确定取消添加当前词条吗？')) {
          return
        }
      } else if (this.actioning === 2) {
        if (!confirm('确定取消编辑当前词条吗？')) {
          return
        }
      }
      if (actioning === 1 || actioning === null) this.checkingword = null
      this.actioning = actioning
    },
    spreadChange () {
      this.spread = !this.spread
    }
  }
}
</script>

<style lang="stylus" scoped>
.home {
  background: #ffffff;
  /* 设置最小宽度以防止网页放大时错位 */
  min-width: 1116px;
  width: 100%;
  max-width: 1600px;
  text-align: left;
  display: inline-block;
  position: relative;
  border-radius: 8px;

  .fade-enter-active {
    transition: opacity 0.3s;
  }

  .fade-enter {
    opacity: 0;
  }

  .home-header {
    min-height: 80px;
    box-sizing: border-box;
    padding: 20px 0;
    padding-left: 20px;
    border-bottom: 1px solid #c0c0c0;
    box-sizing: border-box;
    display: flex;
    position: relative;
    position: absolute;
    width: 100%;
    z-index: 1;
    background: #fff;
    user-select: none;
  }

  .home-body {
    padding: 0 30px;
    padding-top: 102px;
    display: flex;
    height: calc(100vh - 32px);
    min-height: 600px;
    max-height: 800px;
    box-sizing: border-box;
    top: 90px;
  }
}
</style>



